import React, { Component , useState } from "react";
import logo from './logo.svg';
import './App.css';
import Keyboard from "simple-keyboard";
import "simple-keyboard/build/css/index.css";
import words_spl from './words_spl.json'

function Header() {
  return (
    <div className="header">
      <div className="header header-right">Righty</div>
      <div className="header-text">SPLordle</div>
      <div className="header header-left">Lefty</div>
      <hr/>
    </div>
  );
}

function Square(props) {

  return (
    <div className={"square "+props.color}>{props.value}
    </div>
  )
}

class GameRow extends React.Component {

  renderSquare(key) {
    return (
      <Square key={this.props.line+'_'+key} color={this.props.squares_color[this.props.line][key]} value={this.props.squares[this.props.line][key]}/>
    )
  }
  render() {
    return (
      <div className="gamerow" style={{"gridTemplateColumns": "repeat("+this.props.wordLength+", 1fr)"}}>
        {[...Array(this.props.wordLength)].map((x, i) =>
          this.renderSquare(i)
        )}
      </div>
  )}
}

function GameBoard(props) {

  return (
          <div className="gameboard">
            {[...Array(6)].map((x, i) =>
              {return (<GameRow key={'row'+i} line={i} squares={props.squares} squares_color={props.squares_color} wordLength={props.wordLength} />)}
            )}
            
          </div>
  )        
}

class KeyBoardSet extends React.Component {
  state = {
    layoutName: "default",
    input: ""
  };

  onChange = input => {
    this.setState({
      input: input
    });
    console.log("Input changed", input);
  };
  
  onKeyPress = button => {
    console.log("Button pressed", button);
  
    /**
     * If you want to handle the shift and caps lock buttons
     */
    if (button === "{shift}" || button === "{lock}") this.handleShift();
  };
  
  handleShift = () => {
    let layoutName = this.state.layoutName;
  
    this.setState({
      layoutName: layoutName === "default" ? "shift" : "default"
    });
  };
  
  onChangeInput = event => {
    let input = event.target.value;
    this.setState(
      {
        input: input
      },
      () => {
        this.keyboard.setInput(input);
      }
    );
  };

  
  render() {
    return (
      <Keyboard
        keyboardRef={r => (this.keyboard = r)}
        onChange={input => this.onChange(input)}
        onKeyPress={button => this.onKeyPress(button)}
        theme={"hg-theme-default hg-layout-default myTheme"}
        layoutName={this.state.layoutName}
        layout={{
          default: [
            "q w e r t y u i o p",
            "a s d f g h j k l",
            "{enter} z x c v b n m {bksp}"
          ]
        }}
      />
  )}
}


function Game(props) {
  //<h3>Current word: {props.currentWord}</h3><br/>
  //<h3>Legit words: {props.legitWords[0]+" - "+props.legitWords[props.legitWords.length-1]+" ("+props.legitWords.length+")"}</h3>
  return (
    <div className="game">
      <GameBoard isPlaying={props.isPlaying} squares={props.squares} squares_color={props.squares_color} wordLength={props.wordLength}/>
    </div>
  );
}

class App extends React.Component {
  constructor(props) {
    super(props);
    const targetWord = this.prepare_word();
    const wordLength = targetWord.length;
    //const words = this.prepare_full_words(wordLength);

    this.state = {
      layoutName: "default",
      input: "",
      currentWord: "",
      currentRow: 0,
      currentCol: 0,
      currentKey: "",
      targetWord: targetWord,
      //legitWords2: ["hello","among","squar","round","legit","bunny"],
      squares : [...Array(6)].map((x, i) => [...Array(wordLength)].map((j, k) => null)),
      squares_color : [...Array(6)].map((x, i) => [...Array(wordLength)].map((j, k) => null)),
      isPlaying : true,
      wordLength: targetWord.length
    };

    // Binding the state update function
    this.letterKey = this.letterKey.bind(this) 
    this.prepare_word();
  }

  keepWord = true;

  dictionary_url(word) {
    const url = 'https://api.wordnik.com/v4/word.json/'+word+'/definitions?limit=1&includeRelated=false&useCanonical=false&includeTags=false&api_key=a2a73e7b926c924fad7001ca3111acd55af2ffabf50eb4ae5'
    return url;
  }

  checkWordOnline = async function checkWordOnline(word) {
    const url = this.dictionary_url(word);
    const prom = await fetch(url);
    if (prom.status == 200) {
      console.log(word+' OK '+prom.status);
      return true;
    }
    else {
      console.log(word+' WRONG '+prom.status);
      return false;
    }
  }

  prepare_word() {
    const zerod = new Date('04 Apr 2022');
    const now = new Date();
    const todays_word_num = Math.round((now.getTime() - zerod.getTime())/3600/24/1000);
    const targetWord = words_spl[todays_word_num];
    return targetWord;
  }

  getKey(key){
    if (this.state.isPlaying) {
      if (key>='a' && key<='z') {
        //console.log('letter');
        this.letterKey(key);
        //console.log("state currentWord:", this.state.currentWord);
      }
      else if (key == "Backspace") {
        //console.log('Backspace');
        this.removeLetter();
        //console.log("state currentWord:", this.state.currentWord);
      }
      else if (key == "Enter") {
        //console.log("Enter");
        if (this.state.isPlaying)
          this.checkWord();
      }
    }
  }

  letterKey(key){ 
    if (this.state.currentCol<=(this.state.wordLength-1)) // Keep same row
    {
      this.state.squares[this.state.currentRow][this.state.currentCol] = key; 
      this.state.currentCol = this.state.currentCol+1;
      this.state.currentWord = this.state.currentWord+key;

      // Changing state 
      this.setState({currentWord : this.state.currentWord,
        squares: this.state.squares,
        currentCol: this.state.currentCol,
        currentRow: this.state.currentRow
      })
    }
  }

  removeLetter() {
    if (this.state.currentWord.length == 0) {return}
    this.state.currentCol = this.state.currentCol-1;
    this.state.squares[this.state.currentRow][this.state.currentCol] = null; 
    this.state.currentWord = this.state.currentWord.substring(0,this.state.currentWord.length-1);

    // Changing state 
    this.setState({currentWord : this.state.currentWord,
      squares: this.state.squares,
      currentCol: this.state.currentCol
    })
  }

  async checkWord() {
    // Short word
    if (this.state.currentWord.length != this.state.wordLength) {
      this.bumpMessage("Short Word");
    }
    
    // Check Target word
    else if(this.state.currentWord === this.state.targetWord) {
      //this.bumpMessage("Correct!");
      this.state.isPlaying = false;
      [...Array(this.state.wordLength)].map((x, i) => 
        this.state.squares_color[this.state.currentRow][i] = 'correct'
      )
      this.setState({isPlaying: this.state.isPlaying, 
        squares_color: this.state.squares_color
      })
    }
    
    // Check if legit word in dictionary
    else {
      const word_online_status = await this.checkWordOnline(this.state.currentWord);
      //console.log(word_online_status);
      //console.log(words_spl);
      if(words_spl.find((x)=>(x==this.state.currentWord)) || word_online_status) {
      //this.bumpMessage("Legit word, move to next line");
        for (let i=0;i<this.state.wordLength;i++) {
            if (this.state.currentWord[i]==this.state.targetWord[i]) {
              this.state.squares_color[this.state.currentRow][i] = 'correct';
            }
            else if(this.state.targetWord.includes(this.state.currentWord[i])) {
              this.state.squares_color[this.state.currentRow][i] = 'present';
            }
            else {
              this.state.squares_color[this.state.currentRow][i] = 'none'
            }
        }
        this.state.currentWord = ""; // Clean the word
        this.state.currentCol = 0; // Reset back to first letter

        if (this.state.currentRow < 5) {
          this.state.currentRow = this.state.currentRow+1; // Go to next word in matrix
        }
        else {
          this.state.isPlaying = false;
          this.bumpMessage("Game Over, word was "+this.state.targetWord);
        }

        this.setState({currentWord : this.state.currentWord,
          currentRow: this.state.currentRow,
          currentCol: this.state.currentCol,
          squares_color: this.state.squares_color,
          isPlaying : this.state.isPlaying
        })
      }
      else {
        this.bumpMessage("Not in word list");
      }
    }
  }

  bumpMessage(message) {
    alert(message);
  }
      
  handleKeyDown = event => {
    const keycode = event.code;
    const key = event.key;
    //console.log('Pressed key: '+key);
    this.getKey(key);
  }
  
  
  render() {
    window.addEventListener('keydown', this.handleKeyDown);
    //console.log(this.words[0]);
    //console.log(this.words[this.words.length-1]);
    return (
      <div className="App">
      <Header/>
      <Game isPlaying={this.state.isPlaying} currentWord={this.state.currentWord} currentKey={this.state.currentKey} squares={this.state.squares} squares_color={this.state.squares_color} wordLength={this.state.wordLength}/>
    </div>
    )
  }
}

export default App;
